.ToDoListDelete {
    display: flex;
    align-items: center;
    margin: 10px 0px;
}
.deleteButton:hover{
    background-color:red;
    border:transparent;
}
.deleteButton{
    width: 40px;
    height: 30px;
    background-color: rgb(215, 101, 230);
    border-radius: 1rem;
    border: transparent;
    padding: 2px 0px;
    color: white;
    font-weight: bolder;
    margin-left: 30px;
}